.search__input{
    display:flex;
    align-items: center;
    border:1px solid lightgrey;
    height: 30px;
    padding: 10px 15px;
    width:500px;
    margin: 0px auto;
    margin-top: 40px;
    border-radius: 999px;
}

.search__input>input{
    flex:1;
    padding: 8px 13px;
    font-size: medium;
    border:0;
    outline:0;
}
.search__buttons{
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.search__buttons button{
    margin: 5px;
    background-color:#f8f8f8 !important;
    text-transform: inherit;
}

